.offerings {
  padding-top: 413px;

  @media screen and (min-width: 768px) {
    padding-top: 504px;
  }
  @media screen and (min-width: 1280px) {
    margin-left: auto;
    flex-basis: 48.5%;
    justify-content: left;

    padding-top: 160px;
  }
}

.offerings-container-desktop {
  @media screen and (min-width: 1280px) {
    display: flex;
  }
}

.offerings-title {
  margin-bottom: 28px;

  @include fonts(28px, 33px, 700);
  color: var(--main-txt-cl);

  @media screen and (min-width: 768px) {
    margin-bottom: 20px;
    @include fonts(42px, 49px, 700);

    max-width: 500px;
  }

  @media screen and (min-width: 1280px) {
    @include fonts(42px, 50px, 700);
    max-width: 526px;
  }
}

.offerings-list {
  margin-bottom: 28px;

  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: row;
    margin-bottom: 40px;

    gap: 116px;
  }

  @media screen and (min-width: 1280px) {
    gap: 67px;
  }
}

.offerings-list__name {
  margin-bottom: 16px;
  margin-top: 8px;

  @include fonts(18px, 21px, 700);

  @media screen and (min-width: 1280px) {
    max-width: 252px;
    @include fonts(18px, 25px, 700);
  }
}

.offerings-list-item {
  &:not(:last-child) {
    @media screen and (max-width: 767px) {
      margin-bottom: 28px;
    }
  }

  @media screen and (min-width: 768px) {
    max-width: 252px;
  }
}

@mixin offering-icon(
  $bg-im,
  $dp: block,
  $w: 48px,
  $h: 48px,
  $bd-r: 50%,
  $bg-cl: #ffffff,
  $bg-rep: no-repeat,
  $bg-pos: center
) {
  display: $dp;
  width: $w;
  height: $h;
  border-radius: $bd-r;
  background-color: $bg-cl;
  background-image: $bg-im;
  background-repeat: $bg-rep;
  background-position: $bg-pos;
}

.offerings__item-deivery {
  &:before {
    content: '';

    @include offering-icon(url(../images/offerings/rocket.svg));
  }
}
.offerings__item-order {
  &:before {
    content: '';
    @include offering-icon(url(../images/offerings/touch.svg));
  }
}

.offerings-title__description {
  margin-bottom: 40px;
  min-width: 272px;

  @include fonts(14px, 16px, 400);

  @media screen and (min-width: 768px) {
    margin-bottom: 40px;
    @include fonts(16px, 19px, 400);

    max-width: 500px;
  }

  @media screen and (min-width: 1280px) {
    @include fonts(16px, 24px, 400);

    max-width: 526px;
  }
}
.offerings-list__text {
  //min-width: 272px;
  @include fonts(14px, 16px, 400);

  @media screen and (min-width: 768px) {
    margin-bottom: 40px;
  }

  @media screen and (min-width: 1280px) {
    @include fonts(14px, 21px, 400);
  }
}

.offerings-list__text {
  margin-bottom: 0;
}

.offerings-title__description {
  @media screen and (min-width: 768px) {
    min-width: 526px;
  }
}

.offerings-icons {
  position: relative;
  margin-bottom: 8px;
}

.offerings-icons--green {
  position: absolute;
  top: 8px;
  left: 8px;
}

.offerings-background {
  background-repeat: no-repeat;

  @media screen and (min-width: 1280px) {
    background-image: url('../images/offerings/all-apple@1x_lg.png'),
      url('../images/offerings/bcg-offerings@1x_lg.png');
    background-size: 550px 604px, 100% 100%;
    background-position: 16px 177px, top;

    margin-top: -100px;
  }
  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url('../images/offerings/all-apple@2x_lg.png'),
      url('../images/offerings/bcg-offerings@2x_lg.png');
  }

  @media screen and (min-width: 767px) and (max-width: 1279px) {
    margin-top: -100px;

    background-image: url('../images/offerings/all-apple@1x_md-big.png'),
      url('../images/offerings/apple@1x_md-middle.png'),
      url('../images/offerings/apple@1x_md-small.png'),
      url('../images/offerings/bcg-offerings@1x_md.png');
    background-size: 357px, 166px, 108px, 100% 100%;
    background-position: 32px 150px, 418px 266px, 628px 340px, center;
    //background-position: 32px 250px, 418px 366px, 628px 440px, center;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('../images/offerings/all-apple@2x_md-big.png'),
        url('../images/offerings/apple@2x_md-middle.png'),
        url('../images/offerings/apple@2x_md-small.png'),
        url('../images/offerings/bcg-offerings@2x_md.png');
    }
  }

  @media screen and (max-width: 767px) {
    background-image: url('../images/offerings/all-apple@1x_sm.png'),
      url('../images/offerings/bcg-offerings@1x_sm.png');

    background-repeat: no-repeat;
    background-size: 280px 309px, 100% 100%;
    background-position: center 44px, top;

    padding-top: 0;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('../images/offerings/all-apple@2x_sm.png'),
        url('../images/offerings/bcg-offerings@2x_sm.png');
    }
  }
}

