.offerings-button {
  margin-right: 24px;
  font-weight: 500;
  font-size: 13px;
  line-height: calc(20 / 13);
  color: var(--main-txt-cl);
  background-color: #ffffff;
  border: solid 2px var(--icons-cl);
  cursor: pointer;
  font-family: inherit;
  padding: 11px 22px;
  border-radius: 30px;
  //   width: 106px;
  //   height: 40px;
  // transition-property: background-color;
  // transition-duration: 250ms;
  // transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    border: solid 2px #597d00;
  }
}

.offer__btn {
  display: inline-block;
}

.read-button {
  display: inline-flex;
  align-items: center;
  background-color: var(--background-cl);
  // width: 83px;
  height: 34px;
  border: transparent;

  font-size: 13.67px;
  color: var(--mobile-menu-cl);

  @media screen and (min-width: 1280px) {
    display: none;
  }
}

.read-button__icon {
  @media screen and (min-width: 1280px) {
    display: none;
  }
}
