.pages__link {
  color: var(--mobile-menu-cl);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14286;
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.pages__link:hover, .pages__link:focus {
  color: var(--accent-cl);
  font-weight: 500;
