.btn {
  cursor: pointer;
  border: none;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.menu {
  width: 130px;
  height: 255px;
  z-index: 100;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  background-color: var(--hd-ft-bg-cl);
  border-radius: 30px 0 30px 30px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50px;
  right: 20px;
  overflow: hidden;
  box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
}

.menu.is-open {
  opacity: 1;
  pointer-events: initial;
  visibility: visible;
}

@media screen and (min-width: 480px) {
  .menu {
    right: calc(50% - 140px);
  }
}

.menu__pages {
  flex-direction: column;
  display: flex;
}

.menu__item {
  height: 51px;
  cursor: pointer;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.menu__link {
  color: var(--mobile-menu-cl);
  padding: 16px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.1875;
}

