.footer-subscribe__input {
  width: 279px;
  height: 42px;
  border: .85px solid var(--background-cl);
  border-radius: 30px;
  outline: rgba(0, 0, 0, 0);
  padding: 0 0 0 12px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.5;
}

.footer-subscribe__input::-ms-input-placeholder {
  color: #505050;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.5;
}

.footer-subscribe__input::placeholder {
  color: #505050;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.5;
}

.footer-subscribe__input:not(:placeholder-shown) {
  border-color: var(--icons-cl);
}

.footer-subscribe__input:not(:placeholder-shown):required:invalid {
  border-color: var(--inp-err-bd-cl);
}

.footer-subscribe__error-notification {
  opacity: 0;
  align-items: center;
  transition-property: opacity;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 46px;
  left: 0;
}

.footer-subscribe__error {
  color: var(--inp-err-bd-cl);
  font-size: 10px;
  font-weight: 400;
  line-height: 1.5;
}

.footer-subscribe__input:not(:placeholder-shown):required:invalid + .footer-subscribe__error-notification {
  opacity: 1;
}

.footer-subscribe__btn {
  border: .85px solid var(--icons-cl);
  color: var(--main-txt-cl);
  background-color: rgba(0, 0, 0, 0);
  padding: 11px 22px;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.53846;
  transition-property: background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.footer-subscribe__btn:hover, .footer-subscribe__btn:focus {
  background-color: var(--background-cl);
}

